import * as echarts from 'echarts/lib/echarts';

var defaultOption = {
    show: true,

    grid3DIndex: 0,
    // 
    inverse: false,

    // 
    name: '',
    // 
    nameLocation: 'middle',

    nameTextStyle: {
        fontSize: 16
    },
    // 
    nameGap: 20,

    axisPointer: {},

    axisLine: {},
    // 
    axisTick: {},
    axisLabel: {},
    // 
    splitArea: {}
};

var categoryAxis = echarts.util.merge({
    // 
    boundaryGap: true,
    // splitArea: {
        // show: false
    // },
    // 
    axisTick: {
        // If tick is align with label when boundaryGap is true
        // Default with axisTick
        alignWithLabel: false,
        interval: 'auto'
    },
    // axis.axisLabel
    axisLabel: {
        interval: 'auto'
    },
    axisPointer: {
        label: {
            show: false
        }
    }
}, defaultOption);

var valueAxis = echarts.util.merge({
    // 
    boundaryGap: [0, 0],
    // ,  'dataMin' 
    // min: null,
    //  'dataMax' 
    // max: null,
    // 0_min_max
    // scale: false,
    // 5
    splitNumber: 5,
    // Minimum interval
    // minInterval: null

    axisPointer: {
        label: {
        }
    }
}, defaultOption);

// FIXME
var timeAxis = echarts.util.defaults({
    scale: true,
    min: 'dataMin',
    max: 'dataMax'
}, valueAxis);
var logAxis = echarts.util.defaults({
    logBase: 10
}, valueAxis);
logAxis.scale = true;

export default {
    categoryAxis3D: categoryAxis,
    valueAxis3D: valueAxis,
    timeAxis3D: timeAxis,
    logAxis3D: logAxis
};